.menu-slide{width:0px;height:100vh;background-color:#fe724c;-webkit-backdrop-filter:blur(3px);backdrop-filter:blur(3px);position:fixed;top:0;left:0;z-index:10;will-change:width;overflow:hidden}.menu-slide .menu-list{width:100%;padding-left:20px}.menu-slide .menu-list ul{padding:0;margin:0;list-style:none}.menu-slide .menu-list ul li{display:flex;justify-content:flex-end}.menu-slide .menu-list ul a{width:100%;height:60px;display:flex;align-items:center;padding-left:20px;margin-bottom:40px;position:relative;transition:all 400ms;color:white}.menu-slide .menu-list ul a *{color:white}.menu-slide .menu-list ul a ion-icon{font-size:18pt;margin-right:30px}.menu-slide .menu-list ul a.no-borders{border-radius:0 !important}.menu-slide .menu-list ul a.active{width:50%;color:#333;background-color:white;border-radius:23px 0 0 23px}.menu-slide .menu-list ul a.active span{width:100%;height:20px;display:block;position:absolute;right:0;background-color:inherit}.menu-slide .menu-list ul a.active span::before{content:"";height:100%;width:100%;position:absolute;top:0;left:0;background-color:#fe724c}.menu-slide .menu-list ul a.active span:nth-child(1){top:-20px}.menu-slide .menu-list ul a.active span:nth-child(1)::before{border-bottom-right-radius:30px}.menu-slide .menu-list ul a.active span:nth-child(2){bottom:-20px}.menu-slide .menu-list ul a.active span:nth-child(2)::before{border-top-right-radius:30px}.menu-slide .menu-list ul a.active *{color:#333}
