.menu-list{text-align:right;margin-top:30px}.menu-list h4{font-weight:600;margin-bottom:25px}.menu-list .list{margin-top:50px}.menu-list .controls{margin-top:30px}.menu-list .controls .back{margin-right:20px}.menu-list .controls .back,.menu-list .controls .next{height:42px;width:42px;background-color:#fe724c;border-radius:50%;display:flex;align-items:center;justify-content:center;transition:opacity 300ms;cursor:pointer}.menu-list .controls .back i,.menu-list .controls .next i{color:white;font-size:15pt}.menu-list .controls .disable{opacity:0.6}
