.food-item{width:100%;margin-bottom:30px;cursor:pointer}.food-item .img{height:65px;min-width:65px;flex-basis:65px;border-radius:15px;overflow:hidden;display:flex;align-items:center}.food-item .img img{width:100%}.food-item .value{font-size:13pt;color:#707070;font-weight:400}.food-item>a.active .value{font-weight:600;color:#333}@media (max-width: 767.98px){.food-item{text-align:right;justify-content:flex-end}.food-item .img{height:50px;min-width:50px;flex-basis:50px;order:2}.food-item .value{font-size:10pt;order:1}}
