.list-item{border-bottom:1.5px solid #dfe1e6;padding-bottom:20px;margin-bottom:30px}.list-item:last-child{border-bottom:none}.list-item::before{counter-increment:list;content:"0" counter(list);font-size:30pt;color:#fe724c;font-weight:700;-webkit-transform:translate(0, -3%);transform:translate(0, -3%)}.list-item .data{padding-left:20px}.list-item .data p{margin-top:20px}
