.input-time{min-height:55px;width:179px;padding:12px 15px 12px 20px;background-color:#fafbfc;color:#707070;border-radius:20px;border:2px solid #dfe1e6;font-size:14pt;display:flex;align-items:center;justify-content:space-between;text-transform:capitalize;transition:all 300ms;-webkit-user-select:none;-ms-user-select:none;user-select:none;position:relative}.input-time .icon{height:25px;cursor:pointer}.input-time.isFocus{border:2px solid transparent;box-shadow:0 0 5px -1px #fe724c}.input-time ion-icon{font-size:18pt;color:#fe724c}.input-time input{display:none}.input-time .picker{width:300px;height:350px;background-color:white;position:absolute;top:calc(100% + 20px);left:0;box-shadow:0 0 7px -4px #333;border-radius:20px;padding:20px;z-index:2000}.input-time .picker.hide{display:none !important}.input-time .picker .t5{font-size:14pt}.input-time .picker .am,.input-time .picker .pm{border-radius:10px;padding:7px 11px;font-size:10pt;color:white;background-color:#eee;color:#333;transition:all 300ms;cursor:pointer}.input-time .picker .am.active,.input-time .picker .pm.active{background-color:#fe724c;color:white}.input-time .picker .hours{height:200px;width:200px;background-color:#eee;border-radius:50%;position:relative;padding:10px}.input-time .picker .hours .picker-hours{height:36.5%;width:4px;background-color:#fe724c;position:absolute;left:50%;bottom:50%;-webkit-transform-origin:bottom center;transform-origin:bottom center;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}.input-time .picker .hours .picker-hours::before{content:"";width:10px;height:10px;background-color:#fe724c;position:absolute;bottom:0;left:0;border-radius:50%;-webkit-transform:translate(calc(-50% + 2px), 50%);transform:translate(calc(-50% + 2px), 50%)}.input-time .picker .hours .picker-hours::after{content:"";width:25px;height:25px;border:4px solid #fe724c;background-color:#fe724c;position:absolute;top:2px;left:0;border-radius:50%;-webkit-transform:translate(calc(-50% + 2px), -100%);transform:translate(calc(-50% + 2px), -100%)}.input-time .picker .hours .values{height:93%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);z-index:1000}.input-time .picker .hours .values span{display:block;height:50%;position:absolute;bottom:50%;left:50%;font-size:10pt;color:#333;transition:color 300ms 200ms;-webkit-transform-origin:bottom center;transform-origin:bottom center;cursor:pointer}.input-time .picker .hours .values span.active{color:white}.input-time .picker .hours .values span:nth-child(1){-webkit-transform:translate(-50%, 0) rotate(30deg);transform:translate(-50%, 0) rotate(30deg)}.input-time .picker .hours .values span:nth-child(2){-webkit-transform:translate(-50%, 0) rotate(60deg);transform:translate(-50%, 0) rotate(60deg)}.input-time .picker .hours .values span:nth-child(3){-webkit-transform:translate(-50%, 0) rotate(90deg);transform:translate(-50%, 0) rotate(90deg)}.input-time .picker .hours .values span:nth-child(4){-webkit-transform:translate(-50%, 0) rotate(120deg);transform:translate(-50%, 0) rotate(120deg)}.input-time .picker .hours .values span:nth-child(5){-webkit-transform:translate(-50%, 0) rotate(150deg);transform:translate(-50%, 0) rotate(150deg)}.input-time .picker .hours .values span:nth-child(6){-webkit-transform:translate(-50%, 0) rotate(180deg);transform:translate(-50%, 0) rotate(180deg)}.input-time .picker .hours .values span:nth-child(7){-webkit-transform:translate(-50%, 0) rotate(210deg);transform:translate(-50%, 0) rotate(210deg)}.input-time .picker .hours .values span:nth-child(8){-webkit-transform:translate(-50%, 0) rotate(240deg);transform:translate(-50%, 0) rotate(240deg)}.input-time .picker .hours .values span:nth-child(9){-webkit-transform:translate(-50%, 0) rotate(270deg);transform:translate(-50%, 0) rotate(270deg)}.input-time .picker .hours .values span:nth-child(10){-webkit-transform:translate(-50%, 0) rotate(300deg);transform:translate(-50%, 0) rotate(300deg)}.input-time .picker .hours .values span:nth-child(11){-webkit-transform:translate(-50%, 0) rotate(330deg);transform:translate(-50%, 0) rotate(330deg)}.input-time .picker .hours .values span:nth-child(12){-webkit-transform:translate(-50%, 0) rotate(0deg);transform:translate(-50%, 0) rotate(0deg)}.input-time .picker .next,.input-time .picker .back{width:20px;cursor:pointer}.input-time .picker .next.disable ion-icon,.input-time .picker .back.disable ion-icon{transition:color 200ms;color:#c7c7c7 !important}@media (max-width: 767.98px){.input-time{min-height:40px;width:100%;max-width:150px;font-size:11pt;padding:8px 11px 8px 16px}.input-time .picker{width:250px;height:300px}.input-time .picker .hours{height:150px;width:150px}.input-time .picker .hours .picker-hours{width:2px}.input-time .picker .hours .picker-hours::before{height:7px;width:7px;-webkit-transform:translate(calc(-50% + 1px), 50%);transform:translate(calc(-50% + 1px), 50%)}.input-time .picker .hours .picker-hours::after{height:18px;width:18px;-webkit-transform:translate(calc(-50% + 1px), -100%);transform:translate(calc(-50% + 1px), -100%)}.input-time .picker .hours .values{height:90%}.input-time .picker .hours .values span{font-size:7pt}.input-time .icon{height:14pt}.input-time .icon ion-icon{font-size:14pt}.input-time .am,.input-time .pm{font-size:7pt !important}}
