.PricingCard{width:100%;max-width:367px;min-height:423px;padding:30px;border:2px solid #dfe1e6;border-radius:20px}.PricingCard .info{border-bottom:1px solid #dfe1e6}.PricingCard .info .icon{width:100px}.PricingCard .info .icon img{width:100%}.PricingCard .info .data{margin-left:20px}.PricingCard .info .data .name{font-size:26pt;font-weight:500}.PricingCard .list ul{list-style:none;margin:0;padding:0;padding-left:20px}.PricingCard .list ul li{margin-bottom:10px;position:relative}.PricingCard .list ul li::before{content:"";width:10px;height:10px;margin-right:10px;position:absolute;top:0;left:0;border-radius:50%;background-color:#333;-webkit-transform:translate(-20px, 100%);transform:translate(-20px, 100%)}
