.cart-item{padding-bottom:7px;margin-bottom:7px;box-shadow:0 4px 3px -4px #dfe1e6;transition:opacity 300ms;z-index:300}.cart-item.dragging{opacity:0.5;max-width:170px;box-shadow:none}.cart-item .image{height:40px;width:50px;min-height:40px;min-width:40px;max-height:40px;max-width:40px;border-radius:7px;overflow:hidden}.cart-item .image img{width:100%;height:auto}.cart-item .headline{font-size:11pt;color:#333}
