.main-navbar .cart .menu-cart,.main-navbar .user .menu-user{height:300px;width:200px;padding:20px;background-color:white;position:absolute;top:calc(100% + 20px);right:10%;border-radius:10px 0px 10px 10px;box-shadow:0 0 7px -1px rgba(109,108,108,0.486);z-index:200}.main-navbar .cart .hide.menu-cart,.main-navbar .user .hide.menu-user{display:none !important}.main-navbar .cart .menu-cart::before,.main-navbar .user .menu-user::before{content:"";height:20px;width:20px;position:absolute;right:0;top:0;border-bottom:15px solid #fe724c;border-left:10px solid transparent;border-right:10px solid transparent;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)}.main-navbar .cart .menu-cart a,.main-navbar .user .menu-user a{text-align:center;color:#fe724c;font-size:13pt;transition:color 300ms}.main-navbar .cart .menu-cart a:hover,.main-navbar .user .menu-user a:hover{color:#e25b37}.main-navbar .cart,.main-navbar .user{height:45px;width:45px;background-color:#fe724c;border-radius:15px;position:relative;cursor:pointer}.main-navbar{height:100px}.main-navbar .menu img{height:20px;margin-top:-5px}.main-navbar .logo img{height:36px}.main-navbar .links ul{padding:0;margin:0;list-style:none}.main-navbar .links ul li{display:inline-block;margin-right:50px}.main-navbar .links ul li:last-child{margin-right:0px}.main-navbar .links ul li a{font-size:16pt;font-weight:600;color:#26120c;text-decoration:none;transition:color 300ms}.main-navbar .links ul li a:hover{color:#707070}.main-navbar>a{margin-left:50px}.main-navbar .cart{margin-left:20px}.main-navbar .cart img{height:50%}.main-navbar .cart .menu-cart .list{overflow-y:scroll}.main-navbar .cart .menu-cart .list::-webkit-scrollbar{display:none}.main-navbar .cart .menu-cart .see-more{padding-top:10px}.main-navbar .user{margin-left:50px}.main-navbar .user .icon ion-icon{color:white;font-weight:900;font-size:20pt;margin-top:5px}.main-navbar .user .menu-user .name{font-size:15pt;font-weight:500;color:#333}.main-navbar .user .menu-user .name .val{font-size:12pt;font-weight:400;color:#707070;margin-top:2px}@media (max-width: 991.98px){.main-navbar{height:100px}.main-navbar .logo img{height:30px}.main-navbar .cart,.main-navbar .user{height:35px;width:35px;margin-left:25px;border-radius:13px}.main-navbar .cart .icon ion-icon,.main-navbar .user .icon ion-icon{font-size:13pt !important}}@media (max-width: 767.98px){.main-navbar{height:67px}.main-navbar .menu img{height:13px}.main-navbar .logo img{height:25px}.main-navbar .cart,.main-navbar .user{height:25px;width:25px;margin-left:20px;border-radius:9px}.main-navbar .cart .icon ion-icon,.main-navbar .user .icon ion-icon{font-size:11pt !important}}
