.counter .counter{padding:10px;border:1px solid #dfe1e6;border-radius:30px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.counter .counter .value{color:#333;font-size:15pt;-webkit-user-select:none;-ms-user-select:none;user-select:none}.counter .counter .add,.counter .counter .remove{display:flex;align-items:center}.counter .counter .add.disable ion-icon,.counter .counter .remove.disable ion-icon{opacity:0.5}.counter .counter ion-icon{font-size:20pt;color:#fe724c;font-weight:900;cursor:pointer;transition:opacity 300ms}
