.SelectBox{min-height:55px;width:217px;padding:12px 15px 12px 20px;background-color:#fafbfc;border-radius:20px;border:2px solid #dfe1e6;font-size:14pt;color:#707070;display:flex;align-items:center;justify-content:space-between;text-transform:capitalize;transition:all 300ms;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;position:relative}.SelectBox.isFocus{border:2px solid transparent;box-shadow:0 0 5px -1px #fe724c}.SelectBox select{display:none}.SelectBox .con{width:100%;position:absolute;top:calc(100% + 20px);left:0;overflow:hidden;box-shadow:0 0 7px -4px #333;border-radius:20px;transition:max-height 300ms;will-change:max-height;z-index:101}.SelectBox .con .slider{background-color:white;padding:30px 20px}.SelectBox .con .slider .select-item{padding-bottom:25px;font-size:12pt;color:#707070b6;transition:all 300ms}.SelectBox .con .slider .select-item.active{color:#fe724c;-webkit-transform:translate(30px, 0);transform:translate(30px, 0)}.SelectBox .con .slider .select-item:last-child{padding-bottom:0px}@media (max-width: 767.98px){.SelectBox{min-height:40px;width:100%;max-width:170px;font-size:11pt;padding:8px 11px 8px 16px}.SelectBox .con .slider{padding:20px 15px}.SelectBox .con .slider .select-item{font-size:10pt}}
