section.Gallery .row{margin-bottom:30px}section.Gallery .image-item{max-height:337px;border-radius:10px;overflow:hidden;display:flex;justify-content:center;align-items:center}section.Gallery .image-item:hover img{-webkit-transform:scale(1.1);transform:scale(1.1)}section.Gallery .image-item img{width:100%;transition:all 500ms}section.Gallery .image-item.big{width:100%;height:337px}section.Gallery .image-item.small{width:100%;height:231px}@media (max-width: 991.98px){section.Gallery .row{margin:0px}section.Gallery .image-item{max-height:337px;margin-top:20px}section.Gallery .image-item.big{height:350px}section.Gallery .image-item.small{height:250px;width:70%}section.Gallery .image-item img{height:100%;width:auto}}@media (max-width: 767.98px){section.Gallery .row{margin:0}section.Gallery .image-item{margin-top:20px}section.Gallery .image-item img{height:100%;width:auto}section.Gallery .image-item.big{height:300px}section.Gallery .image-item.small{height:200px;width:70%}}
