section.featured-foods{min-height:100vh;padding-top:100px}section.featured-foods .content{margin-top:75px}section.featured-foods .content .set-section .main-categories{width:100%}section.featured-foods .content .set-section .main-categories .list{width:100%;margin-bottom:70px;border-right:2px solid #dfe1e6}section.featured-foods .content .set-section .main-categories .list ul{padding:0;margin:0;list-style:none}section.featured-foods .content .set-section .main-categories .list ul li{margin-bottom:30px}section.featured-foods .content .set-section .main-categories .list ul li a{font-size:20pt;font-weight:600;color:#707070;transition:color 300ms}section.featured-foods .content .set-section .main-categories .list ul li a.active{color:#fe724c}section.featured-foods .content .set-section .categories{width:100%;max-width:250px}section.featured-foods .content .set-section .categories .label{margin-bottom:15px}section.featured-foods .content .set-section .categories .label ion-icon{margin-right:5px;color:#fe724c;font-size:18pt}@media (max-width: 767.98px){section.featured-foods .content .set-section .main-categories{padding:0}section.featured-foods .content .set-section .main-categories .list{border:none}section.featured-foods .content .set-section .main-categories .list ul li a{font-size:15pt}section.featured-foods .content .set-section .counter{display:none !important}}
