.ask-item{padding:30px 30px;background-color:#2f2725;border-radius:20px}.ask-item .ask{font-size:17pt;font-weight:500;color:white}.ask-item .ask ion-icon{font-size:20pt;color:#fe724c;cursor:pointer}.ask-item .con{max-height:0;line-height:30pt;color:#707070;font-size:12pt;overflow:hidden;transition:max-height 300ms;will-change:max-height}.ask-item .con .answer{padding-top:20px;border-top:2px solid rgba(204,198,198,0.479)}@media (max-width: 767.98px){.ask-item{padding:20px 20px}.ask-item .ask{font-size:11pt}.ask-item .answer{font-size:10pt;line-height:23pt}}
