.login-page{width:100%;min-height:100vh;will-change:margin-left}.login-page .row{transition:all 1s}.login-page .circle{height:50px;width:50px;background-color:#fe724c;top:50%;right:0;-webkit-transform:scale(0);transform:scale(0);z-index:999}.login-page .photo{width:100%;min-height:100vh;height:100%;background-position:center;background-repeat:no-repeat;background-size:cover}.login-page .content{width:-webkit-max-content;width:max-content;padding:150px 0 120px 0}.login-page .content .or{width:100%}.login-page .content .or::before,.login-page .content .or::after{content:"";height:2px;width:10%;background-color:#dfe1e6;display:block;margin-right:20px}.login-page .content .or::after{margin-right:0px;margin-left:20px}.login-page .content .form form .forget{text-align:right}.login-page .content .form form .forget a{text-decoration:none}@media (max-width: 991.98px){.login-page{min-height:calc(100vh - 100px)}.login-page .content{width:95%;padding:20vh 0 120px 0}.login-page .content .form{padding:0 5%}}@media (max-width: 767.98px){.login-page .photo{display:none}.login-page .content{padding:100px 0 120px 0}.login-page .content .form{padding:0 5%}}
